@mixin base {
    align-items: center;
    background-image: url('../images/content-pilot.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    justify-content: center;
    
    @media screen and (min-width: 768px) {
    }

    @media screen and (min-width: 1100px) {
    }

    @media screen and (min-width: 1330px) {
    }

    @media screen and (min-width: 1500px) {
    }

    @media screen and (min-width: 1800px) {
    }
}

.projects-10 {
    @include base;

    p {
        font-size: 3.2rem;
        max-width: 120rem;
        text-align: center;
    }

    &__content {
        h1 {
            font-size: 4.8rem;
            font-weight: 600;
            line-height: 1;
            margin-bottom: 3rem;
            text-align: center;
            text-transform: uppercase;
        }
    }

    &__image {
        height: auto;
        margin-bottom: 6.6rem;
        width: 68.5rem;
    }
}

.projects-10--unum {
    @extend .projects-10;
    
    background-image: none;
    background-color: #627f95;
}